<template>
  <transition name="notification-fade">
    <div class="pop-notification"
         v-if="visible">
      <div class="message">{{message}}</div>
      <button @click="handleClick"
              class="close"><img src="data:image/png;base64,"></button>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'notify',
  props: {
    message: {
      type: String,
      default: ''
    },
    duration: {
      type: Number,
      default: 2500
    }
  },
  data() {
    return {
      visible: false
    }
  },
  computed: {},
  watch: {},
  methods: {
    handleClick() {
      this.hide()
    },
    show() {
      this.visible = true
    },
    hide() {
      this.visible = false
      this.$emit('onHide')
    }
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      if (this.duration) {
        setTimeout(() => {
          this.hide()
        }, this.duration)
      }
    })
  },
  updated() {},
  destroyed() {}
}
</script>

