import Vue from 'vue';
import toastVue from './main.vue';

const ToastConstructor = Vue.extend(toastVue);

let instance = null;
let seed = 1;

let myToast = options => {
  let message = typeof options === 'string' ? options : options.message; //  
  let autoClose = options.autoClose === undefined ? true : options.autoClose; // 
  let duration = options.duration || 2000; //  2000
  let callBack = options.callBack; // 
  let style = options.style || 'A'; // toast  A B

  let id = 'toast_' + seed++;
  instance = new ToastConstructor();
  instance.id = id;
  instance.vm = instance.$mount(); // vue,
  document.body.appendChild(instance.vm.$el); // DOM API,append

  instance.timeout = 0;
  instance.message = message;
  instance.duration = duration;
  instance.autoClose = autoClose;
  instance.style = style;
  if (autoClose) {
    Vue.nextTick(() => {
      instance.timeout = setTimeout(function() {
        callBack();
        instance.hide();
      }, duration);
    });
  }
  instance.show();
};

export default myToast;
