<template>
  <transition name="alert-fade">
    <div class="qun-alert"
         v-if="visible"
         @click.self="handleHide"
         @touchmove.prevent>
      <div class="container">
        <i class="icon-close"
           @click="handleHide">
          <img src="data:image/png;base64,"></i>
        <slot></slot>
        <template v-if="!isSlot">
          <h3 class="title"
              v-if="title">{{title}}</h3>
          <span :class="['msg',type]"
                v-html="message"></span>
        </template>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'Alert',
  props: {
    type: {
      default: 'info',
      type: String,
      validator: function(value) {
        // 
        return ['info', 'warn', 'error', 'ok'].indexOf(value) !== -1
      }
    },
    title: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      visible: true,
      timer: null,
      oldHash: window.location.hash
    }
  },
  computed: {
    isSlot() {
      return this.$slots.default
    }
  },
  methods: {
    handleHide() {
      this.hide()
    },
    show() {
      this.visible = true
    },
    hide() {
      this.visible = false
      clearInterval(this.timer)
    },
    checkRouter() {
      this.timer = setInterval(() => {
        if (window.location.hash !== this.oldHash) {
          this.hide()
        }
      }, 100)
    }
  },
  mounted() {
    this.checkRouter()
  }
}
</script>
<style lang="scss" scoped>
