  .container {
    width: $message-box-width;
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2em 1em;
    background-color: $white;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    border-radius: 0.4em;
    min-height: 5em;
    @include MQ(md) {
      width: 26em;
    }
    .icon-close {
      position: absolute;
      right: 1em;
      top: 1em;
      width: 1em;
      height: 1em;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .title {
      margin-bottom: 1em;
      font-size: 1.2em;
    }
    .msg {
      font-size: 1em;
      color: $black;
      line-height: 1.2;
      &.info {
        color: $black;
      }
      &.warn {
        color: $yellow;
      }
      &.error {
        color: $red;
      }
      &.ok {
        color: $green;
      }
    }
  }
}
</style>
