import Vue from 'vue';
import PromptVue from './main.vue';

const PromptConstructor = Vue.extend(PromptVue);

let instance = null;
let seed = 1;
let myPrompt = options => {
  let config = { ...options };
  // let title = options.title;
  // let confirmFunc = options.confirm;
  let id = 'prompt_' + seed++;
  instance = new PromptConstructor();
  instance.id = id;
  instance.vm = instance.$mount(); // vue,
  document.body.appendChild(instance.vm.$el); // DOM API,append

  Object.keys(config).forEach(k => {
    instance[k] = config[k];
  });
  instance.show();
};

export default myPrompt;
